<template>
  <v-layout row justify-center>
    <v-toolbar dark primary absolute >
      <v-btn icon @click="fecharDialog()" dark>
        <v-icon>keyboard_arrow_left</v-icon>
      </v-btn>
      <v-toolbar-title>Perfil</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn dark flat @click.native="salvar()">Salvar</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-layout column style="">
      <v-flex>
        <v-layout column primary lighten-1 style="padding-top: 50px" class="text-xs-center">
          <v-flex xs12 row>
            <v-avatar
              :tile="false" size="150px" class="white"
            >
              <img src="../../assets/logo.png">
            </v-avatar>
          </v-flex>
          <v-container>
            <v-layout row wrap class="text-xs-center">
              <v-flex offset-md1 offset-lg1></v-flex>
              <v-flex xs12 sm3 md3 lg3><span>Email</span></v-flex>
              <v-flex xs12 sm4 md4 lg4><span>Nome</span></v-flex>
              <v-flex xs12 sm3 md3 lg3><span>Nascimento</span></v-flex>
              <v-flex offset-md1 offset-lg1></v-flex>
            </v-layout>
          </v-container>
          <v-divider></v-divider>
        </v-layout>
      </v-flex>

      <v-flex grey lighten-2 xs12>

        <v-layout row wrap>

          <v-flex offset-md1 offset-lg1></v-flex>
          <v-flex xs12 sm12 md8 lg8 white>
            <v-card class="grey darken-4">
              <v-card-title primary-title>
                <div>
                  <h5>Informações</h5>
                </div>
              </v-card-title>
              <v-card-text>
                <v-form>
                  <v-layout column>
                    <v-flex xs12>
                      <section>
                        <h5>Pessoais</h5>

                          <v-layout row wrap>
                            <v-flex xs12 sm12 md6 lg6 xl6>
                              <v-text-field
                                            label="Nome"

                              ></v-text-field>
                              <v-text-field
                                label="E-mail"

                              ></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm12 md6 lg6 xl6>
                              <v-text-field
                                label="Senha"
                                type="password"
                              ></v-text-field>
                              <v-text-field
                                label="Nascimento"
                              ></v-text-field>
                            </v-flex>
                          </v-layout>

                      </section>

                    </v-flex>
                    <v-divider></v-divider>
                    <v-flex>
                      <section style="margin-top: 15px">

                          <h5>Configuracionais</h5>
                          <v-layout row wrap>
                            <v-flex xs12 sm12 md6 lg6 xl6>
                              <v-switch label="Mandar emails de novos filmes/series"></v-switch>
                              <v-switch label="Receber emails semanais de notícias"></v-switch>
                              <v-switch label="Mostrar que estou online"></v-switch>

                            </v-flex>
                            <v-flex xs12 sm12 md6 lg6 xl6>

                            </v-flex>
                          </v-layout>
                      </section>

                    </v-flex>

                  </v-layout>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <!--<v-spacer></v-spacer>-->
                <!--<v-btn secondary>Cancelar</v-btn>-->
                <!--<v-btn primary>Enviar</v-btn>-->
              </v-card-actions>
            </v-card>
          </v-flex>
          <v-flex offset-md1 offset-lg1></v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-layout>
</template>
<script>
  import {Api} from '../../api'
  import jwtDecode from 'jwt-decode'

  export default {

    data () {
      return {
        userName: '',
        disName: true
      }
    },
    methods: {
      fecharDialog: function () {
        this.$router.go(-1)

      },
      getUsuario: function () {
        let token = localStorage.getItem('iflix-user-token')
        let decoded = jwtDecode(token)
        this.usuario = decoded.usuario
        if (decoded.usuario.nome.length > 15) {
          this.usuario.nome = decoded.usuario.nome.substring(0, 12) + '...'
        } else {
          this.usuario.nome = decoded.usuario.nome
        }
        this.usuario.permissao = decoded.permicao
        this.userName = this.usuario.nome
      },
      salvar:function () {
        console.log('salvou de mentirinha')
        this.fecharDialog();
      }

    },
    mounted () {
      this.getUsuario()
    }
  }
</script>



