<template>

  <v-flex style="height: 100vh; background-image: url('https://conta.nubank.com.br/images/sunrise.jpg');
background-position: center; background-repeat: no-repeat; background-size:cover">


    <v-container>

      <v-flex offset-sm2 offset-md3 offset-lg4 xs12 sm12 md6 lg4 style="position: relative;">
        <v-card style="margin-top: 25%">
          <v-card-text>

            <h3 style="text-align: center;">iFlix</h3>
            <v-alert :class="corAlert" :icon="icone" dismissible v-model="alert">
              {{mensagem}}
            </v-alert>
            <v-flex xs12>
              <v-form v-model="valido" ref="registerForm">
                <v-container grid-list-x1>
                  <v-text-field autofocus
                                label="Nome"
                                required
                                v-model="nome"
                                :rules="regrasDeNome"
                  ></v-text-field>
                  <v-text-field
                    label="E-mail"
                    required
                    :rules="regrasDeEmail"
                    v-model="email"
                  ></v-text-field>
                  <v-text-field
                    name="input-10-1"
                    label="Senha"
                    hint="A senha deve conter ao menos 8 caracteres"
                    min="3"
                    :rules="regrasDeSenha"
                    :append-icon="e1 ? 'visibility' : 'visibility_off'"
                    :append-icon-cb="() => (e1 = !e1)"
                    :type="e1 ? 'password' : 'text'"
                    counter
                    required
                    v-model="senha"
                  ></v-text-field>
                  <v-flex>
                    <v-dialog
                      persistent
                      v-model="modalData"
                      lazy
                      full-width
                    >
                      <v-text-field
                        slot="activator"
                        label="Data de nascimento"
                        v-model="dataNascimentoFormatada"
                        readonly
                      ></v-text-field>
                      <v-date-picker v-model="data_nascimento" locale="pt-br" v-mask="'##/##/####'"
                                     :date-format="date=>
                                      new Date(date).toLocaleDateString()"
                                     :formatted-value.sync="dataNascimentoFormatada">
                        <template slot-scope="{ save, cancel }">
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn flat color="primary" @click="cancel">Cancelar</v-btn>
                            <v-btn flat color="primary" @click="save">OK</v-btn>
                          </v-card-actions>
                        </template>
                      </v-date-picker>
                    </v-dialog>
                  </v-flex>
                  <v-flex>
                    <v-btn block primary large v-on:click="registrar" :disabled="!valido">Registrar</v-btn>
                    <a href="#/login">Voltar ao login</a>
                  </v-flex>

                </v-container>
              </v-form>
            </v-flex>
          </v-card-text>

        </v-card>

      </v-flex>
      <!--<v-footer class="pa-3 grey darken-4">-->
      <!--<p class="text&#45;&#45;white text-md-center">Feito com carinho e sem fins lucrativos © {{ new Date().getFullYear() }}</p>-->
      <!--</v-footer>-->

    </v-container>
  </v-flex>
</template>

<script>
  import { Api } from '../api'
  import {IflixMailer} from '../util/mailer'

  export default {
    name: 'app',
    data () {
      return {
        e1: true,
        valido: false,

        regrasDeNome: [
          (v) => !!v || 'O nome é requirido',
          (v) => v && v.length >= 3 || 'O nome tem que ser maior que 3 caracteres'
        ],
        regrasDeEmail: [
          (v) => !!v || 'O E-mail é requirido',
          (v) => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'O e-mail digitado é inválido'
        ],
        regrasDeSenha: [
          (v) => !!v || 'A senha é requirida',
        ],
        corAlert: '',
        icone: '',
        nome: '',
        modalData: '',
        data_nascimento: '',
        dataNascimentoFormatada: null,
        avatar: 1,
        email: '',
        senha: '',
        alert: false,
        mensagem: ''
      }
    },
    methods: {
      registrar: function () {

        const formData = {
          nome: this.nome,
          data_nascimento: this.data_nascimento,
          avatar: this.avatar,
          email: this.email,
          senha: this.senha
        }
        if (this.$refs.registerForm.validate()) {
          this.$http.post(Api.url + '/usuario', formData, {emulateJSON: true})
            .then(response => {
              if (response.data.type === 'success') {
                this.mensagem = response.data.message
                this.alert = true
                this.icone = 'done'
                this.corAlert = 'green'
                IflixMailer.formData.assunto = 'Usuario Criado com sucesso!'
                IflixMailer.formData.para.email = this.email
                IflixMailer.formData.para.nome = this.nome
                IflixMailer.formData.template = 'novo-usuario'
                IflixMailer.formData.variaveisTemplates.nomepessoa = this.nome
                IflixMailer.formData.url = Api.frontUrl + '/#/login'
              } else if (response.data.type === 'error') {
                this.mensagem = response.data.message
                this.alert = true
                this.icone = 'error'
                this.corAlert = 'red'
              }

            }, response => {
              console.error(response.body)
            })
          IflixMailer.send();
        }

      }
    }
  }
</script>

<style>

</style>



